import { Component, OnInit, Input, ViewChild, TemplateRef } from '@angular/core';
import { PaymentsService } from '../payments.service';
import * as _ from 'lodash';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { Router } from '@angular/router';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'ibss-make-payments',
  templateUrl: './make-payments.component.html',
  styleUrls: ['./make-payments.component.scss']
})
export class MakePaymentsComponent implements OnInit {

  @ViewChild("paymentConfirmation", { static: false }) paymentConfirmation: TemplateRef<any>;
  @ViewChild("couponsList", { static: false }) couponsList: TemplateRef<any>;
  plan: any = null;
  config: typeof CONFIG = CONFIG;
  step: any = 1;
  status = "inprogress";
  orderPlaced = false;
  orderDetails: any = null;
  pricingTypes: any = [];

  selectedPlanType = null;
  selectedType = -1;

  cartItems: any = [];
  cartItemsId: any = [];
  generalPlans: any = [];
  totalCartAmount: any = 0;
  totalDiscountAmount: any = 0;
  finalCartAmount: any = 0;
  roundOffCartAmount: any = 0;
  totalTaxAmount: any = 0;
  mainPlanSelected: any = -1;
  modalRef: BsModalRef;
  couponModalRef: BsModalRef;
  confimrationModal: any = {};
  cartIds: any = [];
  orderID: any = null;
  discountDetails: any = null;
  allCoupons: any = [];
  constructor(
    private payServices: PaymentsService,
    private toastr: ToastrService,
    private router: Router,
    private modalService: BsModalService,
  ) {

  }

  ngOnInit(): void {
    let selectedPlans = this.payServices.getPaymentsDetails();
    this.plan = selectedPlans.planDetails;
    this.generalPlans = selectedPlans.generalPlans;
    console.log("generalPlans", this.generalPlans);
    if (this.config.isLoggedin) {
      this.fecthCoupons();
    }
    const cartItems = localStorage.getItem("cartItems");
    const cartItemsId = localStorage.getItem("cartItemsId");
    const mainPlanSelected = localStorage.getItem("mainPlanSelected");
    if (cartItems != null && cartItems != undefined && cartItems != "" && cartItemsId != null && cartItemsId != undefined && cartItemsId != "") {
      this.cartItems = JSON.parse(cartItems);
      this.cartItemsId = JSON.parse(cartItemsId);
      this.cartItems.forEach((item: any) => {
        this.onAddToCart(item, true);
      });
      this.caluclateFinalAmount();
    }

    if (mainPlanSelected) {
      this.mainPlanSelected = JSON.parse(mainPlanSelected) || -1;
    }

    if (_.isEmpty(this.plan)) {
      this.onBrowsePlans();
    } else {
      this.pricingTypes = this.plan.pricingMaster;
      this.selectedType = this.plan.pricingMaster.length > 0 ? 0 : -1;
      this.selectedPlanType = this.selectedType != -1 ? this.plan.pricingMaster[0].priceType : null;
    }

    const step = sessionStorage.getItem("paymentStep");
    if (step && !_.isEmpty(step)) {
      this.step = JSON.parse(step);
    }

    if (this.step == 3) {
      this.onCheckout();
    }


  }

  fecthCoupons() {
    this.config.toggleLoader(true);
    this.allCoupons = [];
    this.payServices.getDiscount()
      .subscribe((res: any) => {
        console.log("getDashboardData", res);
        console.log("verfiyOTP", res);
        if (res.error && res.error.responseCode === 0) {
          let tempRes = JSON.parse(JSON.stringify(res))
          if (tempRes.serviceData && tempRes.serviceData.length > 0) {
            this.allCoupons = tempRes.serviceData;
          }

          // this.allCoupons = [];

          //   for(let i=0;i<=10;i++) {
          //     this.allCoupons.push({
          //       "couponCode": "COUPON"+i,
          //       "discountId": "Id1",
          //       "discountPercentage": i*5,
          //       "discountTerms": "Description "+i
          //     })
          //   }
        } else {
          if (res.error) {
            if (res.error.businessValidation.length > 0) {
              for (let i = 0; i < res.error.businessValidation.length; i++) {
                this.toastr.error(res.error.businessValidation[i].errorDesc);
              }
            }
            if (res.error.dataValidation.length > 0) {
              for (let i = 0; i < res.error.dataValidation.length; i++) {
                this.toastr.error(res.error.dataValidation[i].errorDesc);
              }
            }
          } else if (res.responseCode == -1) {
            this.toastr.error("Please try again after sometimes");
          }
        }
        this.config.toggleLoader(false);
      }, (err) => {
        this.config.toggleLoader(false);
        this.toastr.error("Please try again after sometimes");
      });
  }

  onCheckout() {
    if (this.config.isLoggedin) {
      this.checkoutCart();
    } else {
      sessionStorage.setItem("goToPayments", JSON.stringify(true));
      this.router.navigate(['get-started']);
    }
  }

  onBrowsePlans() {
    this.payServices.resetPaymentDetails();
    if (this.config.isLoggedin) {
      this.router.navigate(['./main', 'payments', 'plans']);
    } else {
      this.router.navigate(['/'], { fragment: "pricing" });
    }
  }


  onSelectPlanPeriod() {
    this.selectedType = _.findIndex(this.plan.pricingMaster, { priceType: this.selectedPlanType });
  }


  confirm(): void {
    this.onAddToCart(this.confimrationModal.plan, this.confimrationModal.isLocal, true);
    this.confimrationModal = null;
    this.modalRef.hide();
  }

  decline(): void {
    this.confimrationModal = null;
    this.modalRef.hide();
  }

  onAddToCart(plan: any, isLocal: any = false, confirm: any = false) {
    console.log(plan);
    console.log(this.selectedPlanType);
    // this.selectedPlanType
    // this.cartItems.push(plan);
    const selectedType = plan.planType == "General" ? "Existing" : this.selectedPlanType;
    // let priceId = plan.pricingMaster.filter((type: any) => type.priceType === selectedType);
    let priceId = plan.priceId || plan.pricingMaster.filter((type: any) => type.priceType === selectedType);;
    const ItemId = `${plan.planName.replace(/ /g, "")}|${plan.planType.replace(/ /g, "")}`;
    const indx = this.cartItemsId.indexOf(ItemId);
    let item: any = null;

    if (!confirm && this.mainPlanSelected != -1 && plan.planName != "General") {
      this.confimrationModal = {
        plan: plan,
        isLocal: isLocal,
      };

      this.modalRef = this.modalService.show(this.paymentConfirmation, { class: 'modal-sm' });
      return;
    }
    if (indx == -1) {
      item = {
        "planName": plan.planName,
        "planType": plan.planType,
        "priceId": plan.priceId || priceId[0].priceId,
        "price": plan.price || priceId[0].price,
        "quantity": 1,
        "planPeriod": this.selectedPlanType,
      }
      if (!isLocal) {
        this.cartItems.push(item);
        this.cartItemsId.push(ItemId);
        if (this.mainPlanSelected == -1) {
          this.mainPlanSelected = plan.planName == "General" ? -1 : this.cartItemsId.indexOf(indx);
        }
      }
    } else {

      this.cartItems[indx].priceId = plan.priceId || priceId[0].priceId;
      this.cartItems[indx].price = plan.price || priceId[0].price;
      if (!isLocal && plan.planType == 'General') {
        this.cartItems[indx].quantity++;
      }
      item = this.cartItems[indx];
    }

    this.caluclateFinalAmount();
    if (this.config.isLoggedin) {
      this.config.toggleLoader(true);
      this.payServices.addItem(item)
        .subscribe((res: any) => {
          if (res.error && res.error.responseCode === 0) {
            let tempRes = JSON.parse(JSON.stringify(res))
            if (tempRes.serviceData && tempRes.serviceData.cartId && tempRes.serviceData.cartId.length > 0) {
              if (this.cartIds.indexOf(tempRes.serviceData.cartId) == -1) {
                this.cartIds.push(tempRes.serviceData.cartId);
              }
              if (tempRes.serviceData.orderId == -1) {
                this.orderID = tempRes.serviceData.orderId;
              }
            }
          } else {
            if (res.error) {
              if (res.error.businessValidation.length > 0) {
                for (let i = 0; i < res.error.businessValidation.length; i++) {
                  this.toastr.error(res.error.businessValidation[i].errorDesc);
                }
              }
              if (res.error.dataValidation.length > 0) {
                for (let i = 0; i < res.error.dataValidation.length; i++) {
                  this.toastr.error(res.error.dataValidation[i].errorDesc);
                }
              }
            } else if (res.responseCode == -1) {
              this.toastr.error("Please try again after sometimes");
            }
          }
          this.config.toggleLoader(false);
        }, (err) => {
          console.log("error in add to cart", err);
        });
    }

    localStorage.setItem("mainPlanSelected", JSON.stringify(this.mainPlanSelected));
    localStorage.setItem("cartItems", JSON.stringify(this.cartItems));
    localStorage.setItem("cartItemsId", JSON.stringify(this.cartItemsId))

  }

  onQuantity(val: any, indx: any) {
    this.cartItems[indx].quantity = this.cartItems[indx].quantity + val;

    if (this.cartItems[indx].quantity == 0) {

      if (this.mainPlanSelected != -1 && this.cartItems[indx].planName != "General") {
        this.mainPlanSelected = false;
      }



      console.log(this.cartItems[indx]);
      if (this.config.isLoggedin) {
        const item = {
          "serviceData": {
            "cartId": [
              this.cartItemsId[indx]
            ],
            "orderId": this.orderID
          }
        }
        this.payServices.removeItem(item).subscribe();
      }

      this.cartItems.splice(indx, 1);
      this.cartItemsId.splice(indx, 1);

    } else {
      this.onAddToCart(this.cartItems[indx]);
    }
    localStorage.setItem("mainPlanSelected", JSON.stringify(this.mainPlanSelected));
    localStorage.setItem("cartItems", JSON.stringify(this.cartItems));
    localStorage.setItem("cartItemsId", JSON.stringify(this.cartItemsId))
    this.caluclateFinalAmount()
  }
  caluclateFinalAmount() {
    this.totalCartAmount = 0;
    const allItems = JSON.parse(JSON.stringify(this.cartItems));
    allItems.forEach((item: any) => {
      this.totalCartAmount += (item.price * item.quantity);
    });
    this.getFinalCartAmount();
  }

  getFinalCartAmount() {
    this.finalCartAmount =0;
    this.roundOffCartAmount =0;
    this.totalDiscountAmount = 0;
    if (this.totalCartAmount>0) {
      this.finalCartAmount = this.totalCartAmount;
      if (this.discountDetails) {
        this.totalDiscountAmount = this.totalCartAmount * (this.discountDetails.discountPercentage / 100);  
        this.finalCartAmount = this.totalCartAmount - this.totalDiscountAmount;
        
      }
      this.roundOffCartAmount = Math.ceil(this.finalCartAmount);
    }
  }

  checkoutCart() {
    this.config.toggleLoader(true);

    let cartItem = {
      "serviceData": {
        "cartId": this.cartIds,
        "couponCode":this.discountDetails? this.discountDetails.couponCode: "",
        "orderId": this.orderID,
        "totalAmount": this.totalCartAmount,
        "totalDiscountAmount": this.totalDiscountAmount,
        "totalItemAmount": this.finalCartAmount,
        "totalRoundOffAmount": this.finalCartAmount,
        "totalTaxAmount": this.totalTaxAmount
      }
    };
    this.payServices.checkout(cartItem)
      .subscribe((res: any) => {
        if (res.error && res.error.responseCode == 0) {
          this.orderPlaced = true;
          this.orderDetails = res.responseData;
        } else {
          if (res.error) {
            if (res.error.businessValidation.length > 0) {
              for (let i = 0; i < res.error.businessValidation.length; i++) {
                this.toastr.error(res.error.businessValidation[i].errorDesc);
              }
            }

            if (res.error.dataValidation.length > 0) {
              for (let i = 0; i < res.error.dataValidation.length; i++) {
                this.toastr.error(res.error.dataValidation[i].errorDesc);
              }
            }
          } else if (res.responseCode == -1) {
            this.toastr.error("Please try again after sometimes");
          }
          this.orderPlaced = false;
        }
        this.step = 3;
        this.payServices.resetPaymentDetails();
        this.config.toggleLoader(false);
      }, (error) => {
        console.log('signup failure', error);
        this.step = 2;
        this.orderPlaced = false;
      })
  }

  onPlaceOrder() {
    this.config.toggleLoader(true);
    this.payServices.placeOrder(this.orderID)
      .subscribe((res: any) => {
        if (res.error && res.error.responseCode == 0) {
          this.orderPlaced = true;
          this.orderDetails = res.responseData;
        } else {
          if (res.error) {
            if (res.error.businessValidation.length > 0) {
              for (let i = 0; i < res.error.businessValidation.length; i++) {
                this.toastr.error(res.error.businessValidation[i].errorDesc);
              }
            }

            if (res.error.dataValidation.length > 0) {
              for (let i = 0; i < res.error.dataValidation.length; i++) {
                this.toastr.error(res.error.dataValidation[i].errorDesc);
              }
            }
          } else if (res.responseCode == -1) {
            this.toastr.error("Please try again after sometimes");
          }
          this.orderPlaced = false;
        }
        this.step = 4;
        this.payServices.resetPaymentDetails();
        this.config.toggleLoader(false);
      }, (error) => {
        console.log('signup failure', error);
        this.step = 4;
        this.orderPlaced = false;
        this.payServices.resetPaymentDetails();
        this.config.toggleLoader(false);
      })
  }

  onViewCoupons() {
    this.couponModalRef = this.modalService.show(this.couponsList, { class: 'modal-md' });
  }

  onSelectCoupon(coupon:any) {
    this.discountDetails = coupon;
    this.getFinalCartAmount();
    this.couponModalRef.hide();
  }
}
